<template>
  <header id="header">
    <!--
    <button v-on:click="show = !show">
      メニュー
    </button>
    <transition name="fade">
      <p v-if="show">一覧へ</p>
    </transition>
    -->
    {{head}}
    <!--
    <button id="header-button" v-on:click="logout">ログアウト</button>
    -->
  </header>
</template>

<script>
import firebase from 'firebase'
import router from '../router'

export default {
  name: 'header',
  data () {
    return {
      head: 'ポータルサイトのプロトタイプ',
      show: false
    }
  },
  methods: {
    logout: function (event) {
      firebase.auth().signOut().then(function () {
        router.push('/login')
      })
    }
  }
}
</script>


<style scoped>
header {
  text-align: center;
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
}

button {
  text-align: right;
}
</style>



