import Vue from 'vue'
import firebase from 'firebase'
import router from '../../router'
import PropertyStore from '../../models/PropertyStore.js'

export var NameHeader = Vue.extend({
  name: 'name_header',
  template: '<div class="name">保護者名:{{ sharedState.state.property.name}}</div>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputText = Vue.extend({
  name: 'name_input',
  template: '<input v-model="sharedState.state.property.name" placeholder="保護者名を入力してください">保護者名</input>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var EmailHeader = Vue.extend({
  name: 'email_header',
  template: '<div class="email">メールアドレス:{{ sharedState.state.property.email }}</div>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputEmail = Vue.extend({
  name: 'email_input',
  template: '<input v-model="sharedState.state.property.email" placeholder="メールアドレスを入力してください">メールアドレス</input>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputButton = Vue.extend({
  name: 'input-button',
  template: '<button v-on:click="validate">確認</button>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  },
  methods: {
    validate: function (event) {
      console.log(this.$el)
      var valid = false
      try {
        valid = this.$data.sharedState.validate()
        if (valid) {
          this.$router.push('/confirm')
        }
      } catch (e) {
        alert(e.message)
      }
    }
  }
})

export var RegistButton = Vue.extend({
  name: 'regist-button',
  template: '<button v-on:click="regist">登録</button>',
  methods: {
    regist: function (event) {
      console.log('called regist')
      firebase.auth().createUserWithEmailAndPassword(
        PropertyStore.state.property.email,
        PropertyStore.state.property.password
      ).then(function (user) {
        console.log('Create acount: ', user)
        router.push('/done')
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
})

export var AgreementButton = Vue.extend({
  name: 'agreement-button',
  template: '<button v-on:click="agreement">同意</button>',
  methods: {
    agreement: function (event) {
      this.$router.push('/input')
    }
  }
})

export var EndpointHeader = Vue.extend({
  name: 'endpoint-header',
  template: '<div class="endpoint">エンドポイント:{{ sharedState.state.property.endpoint }}</div>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var InputPassword = Vue.extend({
  name: 'input-password',
  template: '<input type="password" v-model="sharedState.state.property.password" placeholder="パスワードを入力してください">メールアドレス</input>',
  data: function () {
    return {
      privateState: {},
      sharedState: PropertyStore
    }
  }
})

export var LoginButton = Vue.extend({
  name: 'input-button',
  template: '<button class="button" v-on:click="login">ログイン</button>',
  methods: {
    login: function (event) {
      console.log('called login: ', PropertyStore.state.property)
      firebase.auth().signInWithEmailAndPassword(
        PropertyStore.state.property.email,
        PropertyStore.state.property.password
      ).then(function (user) {
        console.log('login success:', user)
        router.push('/top-menu')
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
})

export var LogoutButton = Vue.extend({
  name: 'logout-button',
  template: '<button class="button" v-on:click="logout">ログアウト</button>',
  methods: {
    logout: function (event) {
      firebase.auth().signOut().then(function () {
        router.push('/login')
      })
    }
  }
})

export var NotificationButton = Vue.extend({
  name: 'notification-button',
  template: '<button class="button" v-on:click="next">お知らせ一覧</button>',
  methods: {
    next: function (event) {
      router.push('/notification-list')
    }
  }
})

export var UpdateButton = Vue.extend({
  name: 'update-button',
  template: '<button class="button" v-on:click="next">保護者情報の更新</button>',
  methods: {
    next: function (event) {
      router.push('/user-update')
    }
  }
})



