<template>
  <div class='input'>
    <main>
      <div>
        メールアドレス:<input-email></input-email><br>
        パスワード:<input-password></input-password><br>
        保護者名:<input-text></input-text><br>
        <input-button></input-button>
      </div>
    </main>
  </div>
</template>

<script>
import Vue from 'vue'
import {InputText, InputButton, InputEmail, EndpointHeader, InputPassword} from './forms/Headers.js'

export default {
  name: 'input',
  data () {
    return {
    }
  }
}
Vue.component('input-email', InputEmail)
Vue.component('input-text', InputText)
Vue.component('input-button', InputButton)
Vue.component('endpoint-header', EndpointHeader)
Vue.component('input-password', InputPassword)
</script>

<style>
main {
  text-align: center;
  margin-top: 40px;
}
</style>




