<template>
  <div id="top-menu">
    <main>
      <div>
        <notification-button></notification-button><br>
        <update-button></update-button>
      </div>
    </main>
  </div>    
</template>

<script>
import Vue from 'vue'
import {NotificationButton, UpdateButton} from './forms/Headers.js'

export default {
  name: 'top-menu',
  data () {
    return {
    }
  }
}
Vue.component('notification-button', NotificationButton)
Vue.component('update-button', UpdateButton)
</script>

<style>
main {
  text-align: center;
  margin-top: 40px;
}

.button {
    display: block;
  position: relative;
  margin: 0 auto;
  height: 100pt;
  width: 200pt;
  border: solid 1px silver;
  border-radius: 0.5rem 0.5rem;
  padding: 0.5rem 1.5rem;
  margin-top: 1rem;
  text-decoration: none;
}
</style>



