<template>
  <div class='terms_of_serivce'>
    <main>
      <p>
        利用規約
      </p>
      <agreement-button></agreement-button>
    </main>
  </div>
</template>

<script>
import Vue from 'vue'
import {AgreementButton} from './forms/Headers.js'
import PropertyStore from '../models/PropertyStore.js'

export default {
  name: 'terms_of_service',
  data () {
    return {
      privateState: {},
      sharedStte: PropertyStore
    }
  }
}
Vue.component('agreement-button', AgreementButton)
console.log(navigator)

window.addEventListener('load', function () {
  if ('serviceWorker' in navigator) {
    navigator.serviceWorker.register('service-worker.js')
    navigator.serviceWorker.ready.then(function (registration) {
      return registration.pushManager.getSubscription().then(function (subscription) {
        if (subscription) {
          return subscription
        }
        return registration.pushManager.subscribe({
          userVisibleOnly: true
        })
      })
    }).then(function (subscription) {
      PropertyStore.state.property.endpoint = subscription.endpoint
      console.log('pushManager endpoint:', PropertyStore.state.property.endpoint)
    }).catch(function (error) {
      console.warn('serviceWorker error:', error)
    })
  } else {
    console.log('ServiceWorker is not supported.')
  }
})
</script>

<style scoped>
main {
  text-align: center;
  margin-top: 40px;
}
</style>



